<template>
    <div class="list container-two">
        <ul class="list__info">
            <li v-for="list in listInfo" :key="list" class="list__info-link" @click="filter(list.filter), filterTypeAdd(list.filter)" 
            :class="{'list__active':list.filter == filterType}">
                {{list.name}}
            </li>
        </ul>
        <div class="list__sort" @mouseover="sortActive = true" @mouseout="sortActive = false" >
            <img src="@/assets/images/top.svg" alt="">
            Сортировка по:
            <transition name="list">
                <ul class="list__sort-list" :class="{'list__sort-list__active':sortActive}">
                    <li @click="sort(), sortDisAdd('all')" :class="{'disabled':sortDis == 'all'}">all</li>
                    <li @click="sort('name'), sortDisAdd('name')" :class="{'disabled':sortDis == 'name'}">name</li>
                    <li @click="sort('price'), sortDisAdd('price')" :class="{'disabled':sortDis == 'price'}">price</li>
                </ul>
            
            </transition>
        </div>
    </div>
        
    <div class="main container-two">
        <h2 class="main__title">Все пиццы</h2>
        <div class="main__box">
            <div class="main__box-card" v-for="user in paginatedUsers" :key="user">
                <img :src="user.imageUrl" alt="">
                <router-link class="main__box-card__title" :to="`pizza/`+user.id">{{ user.title }}</router-link>
                <div class="main__box-card__info">
                    <div class="main__box-card__info-type">
                        <span :class="{'main__box-card__info-type__span':user.types[0]==0, 'main__box-card__info-type__un':user.types[0]!=0, 'active':user.types[0]==0}">тонкое</span>
                        <span :class="{'main__box-card__info-type__un':user.types[0]!=1&&user.types[1]!=1,'main__box-card__info-type__span':user.types[0]==1||user.types[1]==1, 'active':user.types[0]!=0&&user.types[0]==1}">традиционное</span>
                    </div>
                    <div class="main__box-card__info-sm">
                        <span id="span1" @click="user.sizes[0]==26?user.priceTwo = user.price:user.price" :class="{'main__box-card__info-type__span':user.sizes[0]==26, 'main__box-card__info-type__un':user.sizes[0]!=26, 'active':user.sizes[0]==26}">26 см.</span>
                        <span id="span2" @click="user.sizes[0]==30||user.sizes[1]==30?user.priceTwo=user.price+30:user.price" :class="{'main__box-card__info-type__un':user.sizes[0]!=30 && user.sizes[1]!=30,'main__box-card__info-type__span':user.sizes[0]==30||user.sizes[1]==30, 'active':user.sizes[0]==30}">30 см.</span>
                        <span id="span3" @click="user.sizes[0]==40||user.sizes[1]==40||user.sizes[2]==40?user.priceTwo=user.price+50:user.price" :class="{'main__box-card__info-type__un':user.sizes[0]!=40 && user.sizes[1]!=40 && user.sizes[2]!=40,'main__box-card__info-type__span':user.sizes[0]==40 || user.sizes[1]==40 || user.sizes[2]==40,  'active':user.sizes[0]==40}">40 см.</span>
                    </div>
                </div>
                <div class="main__box-card__box">
                    <p class="main__box-card__box-price">от {{ user.amount > 0  ? user.priceTwo * user.amount : user.priceTwo }} ₽</p>
                    <span v-if="user.amount < 1" @click="addCart(user.id,user.amount)">
                        <p>+</p>
                        <p>Добавить</p>
                        <p v-if="user.amount">{{ user.amount }}</p>
                    </span>
                    <span v-else @click="addCart(user.id,user.amount)">
                        <p>+</p>
                        <p>Добавить</p>
                        <p v-if="user.amount">{{ user.amount }}</p>
                    </span>
                </div>
            </div>
        </div>
        <div class="main-page">
            <div class="main-page__page" v-for="page in pages" :key="page" @click="pageClick(page)" :class="{'page__selected': page === pageNumber}">{{page}}</div>
        </div>
    </div>
</template>
<script>
import { mapActions, mapState, mapMutations } from "vuex";
export default {
    data() {
        return {
            currentCard:'',
            sortActive:false,
            usersPerPage: 8,
            pageNumber: 1,
            price:30,
            listInfo: [
                {name:'Все',filter:'all'},
                {name:'Мясные',filter:'Мясные'},
                {name:'Вегетарианская',filter:'Vegetarian'},
                {name:'Гриль',filter:'Гриль'},
                {name:'Острые',filter:'Острые'},
                {name:'Закрытые',filter:'Закрытые'}
            ],
            cartPushBool:true
        }
    },
    computed:{
        ...mapState(["getUsers","cart","filterType","sortDis"]),
        pages(){
            return Math.ceil(this.getUsers.length / 8);
        },
        paginatedUsers(){
            let form = (this.pageNumber - 1) * this.usersPerPage;
            let to = form + this.usersPerPage;
            return this.getUsers.slice(form, to)
        },
    },
    methods: {
        ...mapMutations(["cartPush","cartPlus","filter","sort","filterTypeAdd","sortDisAdd"]),
        pageClick(page){
            this.pageNumber = page
        },
        addCart(id,amount){
            let cartVal = {
                id: id
            }
            this.cartPush(cartVal)
        },
        addCard(val){
            this.cart.push(val)
        }
    },
    mounted(){
    },
}
</script>
<style lang="scss">
.page__selected{
    background: #aeaeae;
    cursor: pointer;
    color: #ffffff;
}
.list-enter-active,
.list-leave-active {
  transition: all 0.5s ease;
}
.list-enter-from,
.list-leave-to {
  opacity: 0;
  transform: translateY(-30px);
}
</style>