<template>
    <div class="cart container-thre">
        <div v-if="cart.length > 0" class="cart__title">
            <div class="cart__title-logo">
                <img src="@/assets/images/cartBlack.png" alt="">
                <h2>Корзина</h2>
            </div> 
            <div @click="clearAll" class="cart__title-del">
                <img src="@/assets/images/delete.svg" alt="">
                <p>Очистить корзину</p>
            </div>
        </div>
        <div v-if="cart.length > 0" class="cart__body">
            <div class="cart__body-item" v-for="list in cart" :key="list">
                <div class="cart__body-item__img">
                    <img :src="list.imageUrl" alt="">
                    <span>
                        <h2>{{ list.title }}</h2>
                        <p>тонкое тесто, 26 см.</p>
                    </span>
                </div>
                <div class="cart__body-item__amount">
                    <span @click="changeCart('-',list.id)">-</span>
                    <p>{{ list.amount }}</p>
                    <span @click="changeCart('+',list.id)">+</span>
                </div>
                <p class="cart__body-item__price">{{ list.price * list.amount }} ₽</p>
                <img @click="delCart(list.id)" class="cart__body-item__close" src="@/assets/images/close.png" alt="">
            </div>
        </div>
        <div v-if="cart.length > 0" class="cart__end">
            <div class="cart__end-info">
                <p class="cart__end-info__all">Всего пицц: {{ cartLength }} шт.</p>
                <p class="cart__end-info__allPrice">Сумма заказа: 900 ₽</p>
            </div>
            <div class="cart__end-buy">
                <router-link to="/pizza" class="cart__end-buy__home">
                    <img src="@/assets/images/left.svg" alt="">
                    Вернуться назад
                </router-link>
                <router-link @click="buyAll" to="/pizza" class="cart__end-buy__buy">Оплатить сейчас</router-link>
            </div>
    
        </div>
        <div v-else class="cart__empty">
            <h2 class="cart__empty-title">Корзина пустая 😕</h2>
            <p class="cart__empty-text">Вероятней всего, вы не заказывали ещё пиццу. Для того, чтобы заказать пиццу, перейди на главную страницу.</p>
            <img src="../assets/images/pustoy.png" alt="">
            <router-link to="/pizza" class="cart__empty-btn">Вернуться назад</router-link>
        </div>
    </div>
</template>

<script>
import { mapState,mapMutations,mapGetters } from "vuex";
export default {
data() {
    return {
        cartBool: true,
    }
},
computed:{
    ...mapState(["getUsers","cart"]),
    ...mapGetters(["cartLength"])
},
methods: {
    ...mapMutations(["cartChange","clearCart","clear","modalEdit"]),
    changeCart(val,id){
        let changeObj = {
            val: val,
            id: id
        }
        this.cartChange(changeObj)
    },
    delCart(id){
        this.clearCart(id)
    },
    clearAll(){
        this.clear()
    },
    buyAll(){
        this.modalEdit(true)
        setTimeout(() => {
            this.modalEdit(false)
        }, 3000);
        this.clear()
    }
},
}
</script>

