import { createStore } from 'vuex'
import axios from 'axios';
export default createStore({
  state: {
    getUsers: [],
    apiLink: `https://63dcd780367aa5a7a4031c92.mockapi.io/Todo`,
    cart: [],
    modalBool: false,
    filterObj: [],
    searchUser:[],
    filterType:'all',
    sortDis:'all',
  },
  mutations: {
    addUsers(state, date) {
      state.getUsers = date
      state.filterObj = date
    },
    filter(state, date){
      const cartObj = []
      state.filterObj.forEach(user => {
        if (date == user.filter) {
          cartObj.push(user)
        }
      });
      if (date != 'all') {
        state.getUsers = cartObj
      }else{
        state.getUsers = state.filterObj
      }
    },
    sort(state, date){
      if (date == 'name') {
        state.getUsers.sort((a, b) => {
          if (a.title > b.title) {
              return 1
          }
          if (a.title < b.title) {
              return -1
          }
        })
      }else if (date == 'price'){
          state.getUsers.sort((a,b)=>a.price - b.price)
      }else{
          state.getUsers.sort((a,b)=>a.id - b.id)
      }
    },
    cartPush(state, date) {
      state.getUsers.forEach(pizza => {
        if (pizza.id == date.id) {
          if (pizza.amount < 1) {
            state.cart.push(pizza)
            pizza.amount++
          }else {
            pizza.amount++
          }
        }
      });
      // state.cart.push(date)
    },
    cartChange(state, date){
      state.getUsers.forEach(pizza => {
        if (pizza.id == date.id) {
          if (date.val == '-' && pizza.amount>1) {
            --pizza.amount
          }else if(date.val == '+' && pizza.amount<10) {
            ++pizza.amount
          }
        }
      });
    },
    clearCart(state, date){
      state.cart.forEach((cart,i) => {
        if (cart.id == date) {
          state.cart.splice(i,1)
          state.getUsers.forEach(pizza => {
            if (pizza.id == date) {
              pizza.amount = 0
            }
          });
        }
      });
    },
    clear(state){
      state.cart = []
      state.getUsers.forEach(pizza => {
        pizza.amount = 0
      });
    },
    modalEdit(state,date){
      state.modalBool = date
    },
    searchPush(state, date){
      let search = date;
      if (!search) {
        state.searchUser = []
        console.log(state.searchUser);
      }else{
        search = search.trim().toLowerCase();
        state.searchUser = state.getUsers.filter((note) => {
          if (note.title.toLowerCase().indexOf(search) != -1) return note;
        });
      }
    },
    filterTypeAdd(state,date){
      state.filterType = date
    },
    sortDisAdd(state,date){
      state.sortDis = date
    }
  },
  actions: {
    async GetApi({ commit, state }) {
      const res = await axios.get(state.apiLink)
      commit("addUsers", res.data)
    }
  },
  getters: {
    cartLength(state){
      return state.cart.length
    },
  }
})
